import { useEffect, useState } from "react";
import { getIndices } from "../api/upload/api";

export default function IndexList({ onSelect }: { onSelect: (id: string) => void }) {
  const [indices, setIndices] = useState([]);

  useEffect(() => {
    getIndices().then(setIndices);
  }, []);

  return (
    <ul className="space-y-2">
      {indices.map((index: any) => (
        <li key={index.id}>
          <button onClick={() => onSelect(index.id)} className="text-blue-600 underline">
            {index.name}
          </button>
        </li>
      ))}
    </ul>
  );
}